






















import { Component, Ref, Vue } from 'vue-property-decorator';
import { Action, State, Getter } from 'vuex-class';
import { IInputInfo, ILoginPayload } from '@/types/interface';
import * as commonTypes from '@/types/common';
import banner from '../../components/Banner.vue';
import inputFormPanel from '../../components/InputFormPanel.vue';
import BaseFooter from '../../components/BaseFooter.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
    BaseFooter,
  },
})

export default class EditMemberProfile extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  @Ref('passwordForm')readonly passwordForm!: inputFormPanel;
  @State('userId') stateUserId !: string;
  @Getter('isMobileLogin') isMobileLogin!: boolean;
  @Action('addMember') addMember!: (params: IInputInfo) => Promise<ILoginPayload>;

  inputList = [commonTypes.INPUT_ID, commonTypes.INPUT_NAME,
    commonTypes.INPUT_PHONE, commonTypes.INPUT_EMAIL, commonTypes.INPUT_FULL_ADDRESS];

  passwordList = [commonTypes.INPUT_PASSWORD, commonTypes.INPUT_NEW_PASSWORD];

  link() {
    this.$router.push('/member');
  }

  save() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    console.log(info);
    if (info) {
      info.UserGuid = this.stateUserId;
      this.addMember(info)
        .then((data: ILoginPayload) => {
          if (data.Code === '0000') {
            this.$router.push('/member/interest_exam');
          }
        });
    }
  }

  associate() {
    this.$router.push('/member/member_association');
  }
}
